@tailwind base;
@tailwind components;
@tailwind utilities;

html {
  scroll-padding-top: 70px;
}
.shadow-bottom {
  box-shadow: 0 6px 6px -6px rgba(0, 0, 0, 0.2);
}

a,
input,
button {
  touch-action: manipulation;
}

.highlight-darkblue::selection {
  background: #2b3951;
}

.animate-listening {
  animation: listening 1s linear infinite;
}

@keyframes listening {
  0% {
    color: red;
  }
  50% {
    color: black;
  }
  100% {
    color: red;
  }
}

@keyframes listening-dark {
  0% {
    color: red;
  }
  50% {
    color: white;
  }
  100% {
    color: red;
  }
}

.dark body {
  color-scheme: dark;
}

.dark .text-gray-500 {
  color: rgb(160 162 167);
}

.dark .animate-listening {
  animation: listening-dark 1s linear infinite;
}

.dark hr {
  border-color: #555;
}

@media print {
  .hide-when-print {
    display: none !important;
  }
}

.react-tooltip {
  max-width: 300px;
  z-index: 10;
}

.animate-wave {
  animation: wave 1s cubic-bezier(0.4, 0, 0.6, 1) infinite;
}

@keyframes wave {
  0%,
  100% {
    transform: rotate(10deg);
  }
  50% {
    transform: rotate(-10deg);
  }
}
.bottom-arrow:after {
  content: ' ';
  position: absolute;
  right: 50%;
  bottom: -8px;
  border-top: 8px solid #5ec269;
  border-right: 8px solid transparent;
  border-left: 8px solid transparent;
  border-bottom: none;
}

.org-welcome-message ul {
  text-align: left;
}
img.error-fallback-gray:before {
  content: ' ';
  display: block;
  height: 100%;
  width: 100%;
  background: #cccccc;
}

@supports (-webkit-touch-callout: none) {
  .h-screen {
    height: 100vh;
    max-height: -webkit-fill-available !important;
  }
}

.custom-bg-gradient-animation {
  background: linear-gradient(
    60deg,
    #f79533,
    #f37055,
    #ef4e7b,
    #a166ab,
    #5073b8,
    #1098ad,
    #07b39b,
    #6fba82
  );
  background-size: 400% 400%;
  animation: custom-bg-gradient-animation 30s ease infinite;
}
@keyframes custom-bg-gradient-animation {
  0% {
    background-position: 0% 30%;
  }
  50% {
    background-position: 100% 30%;
  }
  100% {
    background-position: 0% 30%;
  }
}

.main-chat-input::placeholder {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap !important;
}

[role='button'] {
  cursor: default;
}

.custom-shadow {
  box-shadow:
    rgba(0, 0, 0, 0.071) 0px 12px 28px 0px,
    rgba(0, 0, 0, 0.1) 0px 2px 4px 0px,
    rgba(255, 255, 255, 0.05) 0px 0px 0px 1px inset;
}

@keyframes slideBackground {
  from {
    background-size: 0% 90%;
  }
  to {
    background-size: 100% 100%;
  }
}

@layer utilities {
  .animate-bg {
    -webkit-animation: slideBackground 3s ease forwards;
    animation: slideBackground 3s ease forwards;
  }
}

.ai-bubble {
  -webkit-animation: bubble-action 2s infinite alternate;
  animation: bubble-action 2s infinite alternate;
  rotate: -16deg;
}

@-webkit-keyframes bubble-action {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-3px) translateX(-3px);
  }
}

@keyframes bubble-action {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-3px) translateX(-3px);
  }
}

.custom-model-button {
  animation: custom-model-bg 2s infinite alternate;
}

@keyframes custom-model-bg {
  0% {
    background-color: #65a30d;
  }
  100% {
    background-color: #7abb19;
  }
}

@keyframes typing-and-pause {
  0%,
  25% {
    width: 0;
  }
  50% {
    width: 110px;
  }
  100% {
    width: 110px;
  }
}

@keyframes blink-caret {
  50% {
    border-color: transparent;
  }
}

.typing-animation {
  white-space: nowrap;
  overflow: hidden;
  border-right: 0.15em solid orange;
  width: 0;
  animation:
    typing-and-pause 4s steps(18, end) infinite,
    blink-caret 1s step-end infinite;
}

.group-hover-visible {
  visibility: hidden; /* Hide but it's still taking up space */
  max-height: 0; /* Ensure it's not taking up space */
  opacity: 0;
  transform: scale(0);
  transition:
    visibility 0s linear 0.3s,
    max-height 0.3s ease-out,
    opacity 0.3s ease-out,
    transform 0.3s ease-out;
}

/* Hover state: visible and taking up space */
.group:hover .group-hover-visible {
  visibility: visible;
  max-height: 100px; /* Adjust based on the content size */
  opacity: 1;
  transform: scale(100%);
  transition-delay: 0s;
}

.group:hover .group-hover-visible.delay {
  transition-delay: 0.4s; /* Second item appears after a delay */
}

@keyframes fade-in {
  from {
    transform: translateY(20px);
  }
  to {
    transform: translateY(0);
  }
}
.fade-in {
  animation: fade-in 0.5s ease-out forwards;
}
